.psample_row1 {	height: 120px;}
.psample_row1 {	height: 120px;}
.psample_row2 {	height: 160px;}
.psample_row3 {	height: 160px;}
.psample_row4 {	height: 160px;}

.psample {
  overflow: hidden;
  position: relative;
}
.psample p {
  line-height: 1.3em;
  display: block;
  overflow: hidden;
  margin: 0;
}

.psample span {
  margin-right: .5em;
}

.white_blend {
  width: 100%;
  height: 61px;
  background-image: url(data:image/png;base64,);
  position: absolute;
  bottom: 0;
}
.black_blend {
  width: 100%;
  height: 61px;
  background-image: url(data:image/png;base64,);
  position: absolute;
  bottom: 0;
}
.fullreverse {
  background:  #000 !important;
  color:  #fff !important;
  margin-left: -20px;
  padding-left: 20px;
  margin-right: -20px;
  padding-right: 20px;
  padding: 20px;
  margin-bottom:0;
}


.sample_table td {
  padding-top: 3px;
  padding-bottom:5px;
  padding-left: 5px;
  vertical-align: middle;
  line-height: 1.2em;
}

.sample_table td:first-child {
  background-color: #eee;
  text-align: right;
  padding-right: 5px;
  padding-left: 0;
  padding: 5px;
  font: 11px/12px "Courier New", Courier, mono;
}

code {
  white-space: pre;
  background-color: #eee;
  display: block;
  padding: 10px;
  margin-bottom: 18px;
  overflow: auto;
}


.bottom,.last 	{margin-bottom:0 !important; padding-bottom:0 !important;}

.box  {
  padding: 18px;
  margin-bottom: 18px;
  background: #eee;
}

.reverse,.reversed { background:  #000 !important;color:  #fff !important; border: none !important;}

#bodycomparison {
  position: relative;
  overflow: hidden;
  font-size: 72px;
  height: 90px;
  white-space: nowrap;
}

#bodycomparison div{
  font-size: 72px;
  line-height: 90px;
  display: inline;
  margin: 0 15px 0 0;
  padding: 0;
}

#bodycomparison div span{
  font: 10px Arial;
  position: absolute;
  left: 0;
}
#xheight {
  float: none;
  position: absolute;
  color: #d9f3ff;
  font-size: 72px;
  line-height: 90px;
}

.fontbody {
  position: relative;
}
.arialbody{
  font-family: Arial;
  position: relative;
}
.verdanabody{
